'use client';

import React, { useEffect, useState } from 'react';
import { Card } from '../../../components/UI';
import { Users, Building2, TrendingUp, Activity, Clock, CheckCircle, Mail, Briefcase, MapPin } from 'lucide-react';
import { useUser } from '@clerk/nextjs';
import Link from 'next/link';

const StatCard = ({ title, value, change, changeType, icon: Icon, subtext, color }: any) => {
  const isPositive = changeType === 'positive';

  return (
    <Card className="p-6 hover:shadow-lg transition-shadow duration-200">
      <div className="flex justify-between items-start mb-4">
        <div className="flex-1">
          <span className="text-sm text-gray-500 dark:text-gray-400 font-medium">{title}</span>
        </div>
        <div className={`p-2.5 rounded-lg ${color}`}>
          <Icon size={20} className="text-white" />
        </div>
      </div>

      <div>
        <h3 className="text-3xl font-bold text-gray-900 dark:text-white mb-2">{value}</h3>
        <div className="flex items-center text-sm gap-2">
          {change && (
            <span className={`font-medium ${isPositive ? 'text-green-600 dark:text-green-400' : 'text-gray-500'}`}>
              {change}
            </span>
          )}
          <span className="text-gray-500 dark:text-gray-400">{subtext}</span>
        </div>
      </div>
    </Card>
  );
};

interface DashboardStats {
  contacts: number;
  agencies: number;
  usage: {
    count: number;
    total: number;
    remaining: number;
  };
}

interface Contact {
  id: string;
  firstName: string;
  lastName: string;
  email: string;
  jobTitle?: string;
  department?: string;
  agency?: {
    name: string;
  };
}

interface JobTitleStat {
  title: string;
  count: number;
  percentage: number;
  color: string;
}

export default function Dashboard() {
  const { user } = useUser();
  const [stats, setStats] = useState<DashboardStats | null>(null);
  const [recentContacts, setRecentContacts] = useState<Contact[]>([]);
  const [jobTitleStats, setJobTitleStats] = useState<JobTitleStat[]>([]);
  const [loading, setLoading] = useState(true);
  const [errorMsg, setErrorMsg] = useState<string>('');

  const colors = ['#3B82F6', '#8B5CF6', '#EC4899', '#F59E0B', '#10B981'];

  useEffect(() => {
    const loadDashboardData = async () => {
      try {
        const response = await fetch('/api/dashboard/stats');

        if (!response.ok) {
          const text = await response.text();
          console.error('Dashboard stats response error:', response.status, text);
          throw new Error(`Failed to fetch dashboard stats: ${response.status}`);
        }

        const data: DashboardStats = await response.json();
        setStats(data);

        // Fetch ALL contacts to calculate job title distribution
        const contactsResponse = await fetch('/api/contacts?limit=1000');
        if (contactsResponse.ok) {
          const contactsData = await contactsResponse.json();
          const allContacts = contactsData.contacts || [];

          // Set recent contacts (first 5)
          setRecentContacts(allContacts.slice(0, 5));

          // Calculate job title statistics
          const jobTitleMap = new Map<string, number>();
          allContacts.forEach((contact: Contact) => {
            if (contact.jobTitle) {
              const title = contact.jobTitle.trim();
              jobTitleMap.set(title, (jobTitleMap.get(title) || 0) + 1);
            }
          });

          const totalWithJobTitles = Array.from(jobTitleMap.values()).reduce((a, b) => a + b, 0);
          const jobTitleArray: JobTitleStat[] = Array.from(jobTitleMap.entries())
            .map(([title, count], index) => ({
              title,
              count,
              percentage: Math.round((count / totalWithJobTitles) * 100),
              color: colors[index % colors.length]
            }))
            .sort((a, b) => b.count - a.count)
            .slice(0, 5);

          setJobTitleStats(jobTitleArray);
        }
      } catch (error) {
        console.error('Error loading dashboard data:', error);
        setErrorMsg((error as Error)?.message || 'Unknown error');
      } finally {
        setLoading(false);
      }
    };

    loadDashboardData();
  }, []);

  const usagePercentage = stats ? Math.min(100, Math.round((stats.usage.count / stats.usage.total) * 100)) : 0;
  const isLimitReached = stats && stats.usage.count >= stats.usage.total;
  const remainingPercentage = 100 - usagePercentage;

  // Calculate donut chart segments
  const totalPercentage = jobTitleStats.reduce((sum, stat) => sum + stat.percentage, 0);
  let currentAngle = 0;

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-2xl font-bold text-gray-900 dark:text-white mb-1">
          Dashboard Overview
        </h1>
        <p className="text-gray-500 dark:text-gray-400">
          Welcome back, {user?.firstName || 'User'}! Here's your analytics summary.
        </p>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-4 gap-6">
        <StatCard
          title="Total Agencies"
          value={loading || !stats ? "..." : stats.agencies}
          change="+4"
          changeType="positive"
          subtext="tracked"
          icon={Building2}
          color="bg-blue-600"
        />
        <StatCard
          title="Total Contacts"
          value={loading || !stats ? "..." : stats.contacts.toLocaleString()}
          change="+150"
          changeType="positive"
          subtext="available"
          icon={Users}
          color="bg-purple-600"
        />
        <StatCard
          title="Credits Used"
          value={loading || !stats ? "..." : stats.usage.count}
          change={`of ${stats?.usage.total || 50}`}
          changeType="neutral"
          subtext="today"
          icon={Activity}
          color="bg-orange-600"
        />
        <StatCard
          title="Credits Left"
          value={loading || !stats ? "..." : stats.usage.remaining}
          change="Resets daily"
          changeType="neutral"
          subtext="available"
          icon={CheckCircle}
          color="bg-green-600"
        />
      </div>

      {errorMsg && (
        <div className="p-4 rounded-lg bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 text-sm text-red-700 dark:text-red-400">
          <strong className="font-semibold">Error:</strong> {errorMsg}
        </div>
      )}

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Daily Usage Card */}
        <Card className="p-8 lg:col-span-2">
          <div className="flex items-center justify-between mb-8">
            <div className="flex items-center gap-4">
              <div className="p-3 rounded-xl bg-blue-600">
                <TrendingUp className="text-white" size={28} />
              </div>
              <div>
                <h3 className="text-xl font-bold text-gray-900 dark:text-white">Daily Credit Usage</h3>
                <p className="text-sm text-gray-500 dark:text-gray-400">Track your API consumption</p>
              </div>
            </div>
            <div className="flex items-center gap-2 px-4 py-2 rounded-lg bg-gray-100 dark:bg-gray-700">
              <Clock size={18} className="text-gray-600 dark:text-gray-400" />
              <span className="text-sm font-medium text-gray-700 dark:text-gray-300">
                Resets at midnight
              </span>
            </div>
          </div>

          <div className="flex flex-col md:flex-row items-center gap-8">
            <div className="relative w-64 h-64">
              <svg className="w-full h-full transform -rotate-90" viewBox="0 0 100 100">
                <circle
                  cx="50"
                  cy="50"
                  r="40"
                  fill="none"
                  stroke="currentColor"
                  strokeWidth="8"
                  className="text-gray-200 dark:text-gray-700"
                />
                <circle
                  cx="50"
                  cy="50"
                  r="40"
                  fill="none"
                  stroke="currentColor"
                  strokeWidth="8"
                  strokeLinecap="round"
                  strokeDasharray={`${usagePercentage * 2.51} 251`}
                  className={isLimitReached ? 'text-red-500' : 'text-blue-600'}
                  style={{ transition: 'stroke-dasharray 0.5s ease' }}
                />
              </svg>
              <div className="absolute inset-0 flex flex-col items-center justify-center">
                <span className={`text-4xl font-bold ${isLimitReached ? 'text-red-600' : 'text-blue-600'}`}>
                  {usagePercentage}%
                </span>
                <span className="text-sm text-gray-500 dark:text-gray-400 mt-1">Used</span>
              </div>
            </div>

            <div className="flex-1 space-y-6">
              <div className="grid grid-cols-2 gap-4">
                <div className="p-4 rounded-lg bg-blue-50 dark:bg-blue-900/20">
                  <p className="text-sm text-gray-600 dark:text-gray-400 mb-1">Used Today</p>
                  <p className="text-2xl font-bold text-blue-600 dark:text-blue-400">
                    {stats ? stats.usage.count : 0}
                  </p>
                </div>
                <div className="p-4 rounded-lg bg-green-50 dark:bg-green-900/20">
                  <p className="text-sm text-gray-600 dark:text-gray-400 mb-1">Remaining</p>
                  <p className="text-2xl font-bold text-green-600 dark:text-green-400">
                    {stats ? stats.usage.remaining : 0}
                  </p>
                </div>
              </div>

              <div className="space-y-3">
                <div className="flex justify-between text-sm">
                  <span className="text-gray-600 dark:text-gray-400">Total Daily Limit</span>
                  <span className="font-semibold text-gray-900 dark:text-white">
                    {stats ? stats.usage.total : 50} credits
                  </span>
                </div>
                <div className="h-3 rounded-full bg-gray-200 dark:bg-gray-700 overflow-hidden">
                  <div className="h-full flex">
                    <div
                      style={{ width: `${usagePercentage}%` }}
                      className={`transition-all duration-500 ${isLimitReached ? 'bg-red-500' : 'bg-blue-600'
                        }`}
                    />
                    <div
                      style={{ width: `${remainingPercentage}%` }}
                      className="bg-green-500"
                    />
                  </div>
                </div>
                <div className="flex justify-between text-xs text-gray-500 dark:text-gray-400">
                  <span>Used: {usagePercentage}%</span>
                  <span>Available: {remainingPercentage}%</span>
                </div>
              </div>

              {isLimitReached ? (
                <div className="p-4 rounded-lg bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800">
                  <p className="text-sm font-medium text-red-700 dark:text-red-400">
                    ⚠️ You've reached your daily limit. Upgrade your plan or wait until tomorrow!
                  </p>
                </div>
              ) : (
                <div className="p-4 rounded-lg bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800">
                  <p className="text-sm font-medium text-blue-700 dark:text-blue-400">
                    ✨ You have {stats?.usage.remaining || 0} credits remaining today
                  </p>
                </div>
              )}
            </div>
          </div>
        </Card>

        {/* Job Title Distribution Card */}
        <Card className="p-6">
          <div className="flex items-center justify-between mb-6">
            <div className="flex items-center gap-3">
              <div className="p-2.5 rounded-lg bg-purple-600">
                <Briefcase className="text-white" size={20} />
              </div>
              <div>
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white">Job Titles</h3>
                <p className="text-sm text-gray-500 dark:text-gray-400">Distribution</p>
              </div>
            </div>
          </div>

          {loading ? (
            <div className="flex items-center justify-center h-64">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
            </div>
          ) : jobTitleStats.length > 0 ? (
            <div className="space-y-6">
              {/* Donut Chart */}
              <div className="relative w-48 h-48 mx-auto">
                <svg className="w-full h-full transform -rotate-90" viewBox="0 0 100 100">
                  {/* Background circle */}
                  <circle
                    cx="50"
                    cy="50"
                    r="35"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth="12"
                    className="text-gray-200 dark:text-gray-700"
                  />
                  {/* Colored segments */}
                  {jobTitleStats.map((stat, index) => {
                    const segmentAngle = (stat.percentage / 100) * 360;
                    const circumference = 2 * Math.PI * 35;
                    const dashArray = `${(segmentAngle / 360) * circumference} ${circumference}`;
                    const rotation = currentAngle;
                    currentAngle += segmentAngle;

                    return (
                      <circle
                        key={stat.title}
                        cx="50"
                        cy="50"
                        r="35"
                        fill="none"
                        stroke={stat.color}
                        strokeWidth="12"
                        strokeDasharray={dashArray}
                        style={{
                          transform: `rotate(${rotation}deg)`,
                          transformOrigin: '50% 50%',
                          transition: 'stroke-dasharray 0.5s ease'
                        }}
                      />
                    );
                  })}
                </svg>
                <div className="absolute inset-0 flex flex-col items-center justify-center">
                  <span className="text-2xl font-bold text-gray-900 dark:text-white">
                    {jobTitleStats.reduce((sum, stat) => sum + stat.count, 0)}
                  </span>
                  <span className="text-xs text-gray-500 dark:text-gray-400">Total</span>
                </div>
              </div>

              {/* Legend */}
              <div className="space-y-2">
                {jobTitleStats.map((stat) => (
                  <div key={stat.title} className="flex items-center justify-between">
                    <div className="flex items-center gap-2 flex-1 min-w-0">
                      <div
                        className="w-3 h-3 rounded-full flex-shrink-0"
                        style={{ backgroundColor: stat.color }}
                      />
                      <span className="text-sm text-gray-700 dark:text-gray-300 truncate">
                        {stat.title}
                      </span>
                    </div>
                    <span className="text-sm font-semibold text-gray-900 dark:text-white ml-2">
                      {stat.count}
                    </span>
                  </div>
                ))}
              </div>
            </div>
          ) : (
            <div className="flex flex-col items-center justify-center h-64 text-gray-400">
              <Briefcase size={48} className="mb-2 opacity-50" />
              <p className="text-sm">No job title data</p>
            </div>
          )}
        </Card>
      </div>
    </div>
  );
}